<template>
  <div>
    <h1>컴퍼넌트를 이용한 메모장</h1>
  </div>
  <hr>
  <div id="app">
    <list-page :list="this.list" :setReadMemo="setReadMemo" v-if="this.mode == this.MODE.LIST"></list-page>
    <read-page :currentMemo="selectedMemo" :setMode="setMode" v-if="this.mode == this.MODE.READ"></read-page>
    <write-page :addMemo="addMemo" v-if="this.mode == this.MODE.WRITE_e"></write-page>
  </div>
</template>

<script>
import ListPage from './pages/ListPage.vue'
import ReadPage from './pages/ReadPage.vue';
import WritePage from './pages/WritePage.vue';
import {MODE} from './common.js';

export default {
  name: 'App',
  components: {
    ListPage,
    ReadPage,
    WritePage
  },
  data(){
    return {
      list : ["memo1", "memo2", "memo3"],
      selectedMemo : "",
      mode : "list",
      // MODE : MODE
    }
  },
  methods: {
    setReadMemo(index) {
      this.selectedMemo = this.list[index];
      this.mode = MODE.READ;
    },
    setMode(mode){
      this.mode = mode;
    },
    addMemo(memo){
      this.list.push(memo);
    }
  },
  computed : {
    MODE() {
      return MODE;
    }
  }
}
</script>

